body {
  line-height: 1;
  background-image: url('/img/bg.jpg');
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

header {
  display: flex;
  justify-content: space-between;
  margin: .3cm 0;
}

.user-name {
  margin-left: .5cm;
  font-size: .7cm;
}

.content {
  padding: 0 .7cm;
}

.forms {
  max-width: 10cm;
}

#clock {
  font-size: .5cm;
}

#modal {
  background: rgba(20, 50, 50, 0.97);
  color: #666871;
  text-align: left;
  max-width: 60%;
  top: 10vh;
  left: 0;
  right: 0;
  position: absolute;
  margin: auto;
  padding: 20px;
  pointer-events: auto;
  z-index: 2;  
  position: fixed;
}

#modal input, textarea {
  width: auto;
  margin-bottom: 5px;
  left: 5px;
  right: 5px;
  display: block;
}

textarea {
  min-width: 400px;
  min-height: 75px;
}

#site_name {
  float: left;
  font-size: 24pt;
}

.btn-default {
  background-color: white;
  border-color: #ABABAB;
}
